const navbarBgEl = document.querySelector('.nav-backdrop')
const listClickEl = document.querySelector('.list-click')



console.log(listClickEl);

const modal = (e) => {
    let el = e.target
    if(el.classlist.contains('active')){
        navbarBgEl.style.opacity= 1
        let interval = 100
        let t = setInterval(() => {
            interval--
            if(interval < 0 ){
                navbarBgEl.style.opacity= 0
            }
            navbarBgEl.style.opacity= interval/100
        }, 1);
    } else {
        navbarBgEl.style.opacity= 0
        
    }
    
}

listClickEl.addEventListener('click', modal)